$xsm: "(max-width: 768px)";
$sm: "(min-width: 768px) and (max-width: 992px)";
$md: "(min-width: 992px) and (max-width: 1200px)";
$lg: "(min-width: 1200px)";

$tablet: "(min-width: 768px)";
$desktop: "(min-width: 1440px)";

$img-path: '~assets/img';

$grid-gutter-width:30px;
$vertical-space:60px;

$base-font-size: 16px;
$font: 'Roboto Mono', Helvetica, Arial, sans-serif;
$font-2: 'Roboto', Helvetica, Arial, sans-serif;

$main-color: #1f0e42;
$main-color-l: #473770;
$main-color-m: #2a1956;
$main-color-d: #150a33;

$accent-color: #ff3531;

$secondary-color: #fc4c4c;

$gray-color: #9ca9ba;
$gray-color-l: #ececec;
$gray-color-m: #f7f7f7;
$gray-blue-color: #675f7f;

$green-color: #4dba87;
$green-color-color-l: #cffbE6;

$black: #040407;
$white: #ffffff;

@mixin btn-theme($modifier: dark) {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px;
    
    font-size: 12px;
    text-align: center;
    font-weight: 700;
    letter-spacing: 0.15em;
    text-transform: uppercase;
    color: $white;

    border-radius: 3px;
    cursor: pointer;
    text-decoration: none;
    border: none;

    @if ($modifier == dark) {
        background-color: $main-color-l;

        &:hover,
        &:focus {
            background-color: #341F6A;
        }

        &:active {
            background-image: linear-gradient(180deg, #33118F 50%, rgba(119, 72, 246, 1) 100%);
        }
    } @else if ($modifier == bright) {
        background-image: linear-gradient(55.59deg, #FF0800 -15.98%, #FF5E5E 105.02%);
        box-shadow: 0px 4px 24px rgba(144, 50, 238, 0.2),
        0px 4px 14px rgba(255, 0, 0, 0.2);

        &:hover,
        &:focus {
            background-image: linear-gradient(35.93deg, #FF5954 -15.98%, #FF5E5E 105.02%);
            box-shadow: 0px 6px 14px rgba(144, 50, 238, 0.2),
            0px 8px 14px rgba(255, 0, 0, 0.2);
        }

        &:active {
            background-image: linear-gradient(35.93deg, #FF0800 -15.98%, #DA1313 105.02%);
            box-shadow: none;
        }
    }


    transition: all 300ms ease;

    &:hover,
    &:active,
    &:focus {
        text-decoration: none;
        color: $white;
    }

}


@mixin visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
}

@mixin card {
    background: #FFFFFF;
    box-shadow: 0px 6px 10px rgba(190, 190, 190, 0.1);
    border-radius: 4px;
}

@mixin svg-icon {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin img {
    display: block;
    width: 100%;
    height: auto;
    max-width: 100%;
    max-height: 100%;
}

